import React from 'react';
import '../../App.css';
import Sidebar from "./sidebarBlog.js";
import '../../stylesheets/sidebar.css'
import { isBrowser } from 'react-device-detect';
import { HashRouter, BrowserRouter, Route, Link } from "react-router-dom";
import AllPosts from "./AllPosts.js";
import OnePost from "./OnePost.js";
import Switch from 'react-bootstrap/esm/Switch';

function SidebarIfBrowser() {

  if (isBrowser) {
    return <Sidebar />
  }
  else {
    return null
  }
}


function Blog() {
  return (
    <div className="App" >
      <header className="App-header" width="100%" >
        <div id="blog" width="100%" style={{marginLeft : isBrowser ? "15%" : "0%", marginTop:"0%", marginBottom:"0%"}}>
          <Switch>
            <div>
              <Route component={AllPosts} exact path="/blog" />
              <Route component={OnePost} exact path="/blog/:slug" />
            </div>
          </Switch>
          <SidebarIfBrowser />
        </div>
      </header>
    </div>
  );
}

export default Blog;
