import React from 'react';
import Main from "./components/Main.js";
import Blog from "./components/blog/Blog.js";

import './stylesheets/sidebar.css'
import { isBrowser } from 'react-device-detect';
import { BrowserRouter, HashRouter, Switch, Route, Link } from "react-router-dom";
import { Item, Title, Button } from "./components/textConstants"

function TopBarIfMobile() {

  if (isBrowser) {
    return null
  }
  else {
    return <div style={{ backgroundColor: "white", marginBottom: "0%" }}><Link to="/blog"><Item style={{ marginTop: "0%", color: "black" }} >Blog</Item></Link><Link to="/"><Item style={{ marginTop: "0%", color: "black", marginBottom: "0%" }} >Home</Item></Link></div>
  }
}

function App() {
  return (
    <div className="App" >
      <header className="App-header" width="100%" >
        <div id="home" width="100%" style={{ marginLeft: isBrowser ? "15%" : "0%", marginRight: "0%", marginTop: "0%", padding: "0%" }}>
          <HashRouter id="blog">
            <TopBarIfMobile />
            <div>
            <Switch>
              <Route component={Blog} path="/blog" />

              <Route component={Main} exact path="/" />
            </Switch>
            </div>
          </HashRouter>
        </div>
      </header>
    </div>
  );
}

export default App;
